.clearfix:after {visibility: hidden; display: block; font-size: 0; content: ''; clear: both; height: 0; }

html { -moz-box-sizing: border-box; -webkit-box-sizing: border-box; box-sizing: border-box;}

*, *:before, *:after {box-sizing: inherit}

.srOnly { position: absolute; width: 1px; height: 1px; margin: -1px; border: 0; padding: 0; white-space: nowrap; clip-path: inset(100%); clip: rect(0 0 0 0); overflow: hidden;}

html {font-size: 62.5%;}

.wrapper {max-width: 1280px; width: 90%; margin: 0 auto; padding: 50px 0;}

img {width: 100%; height: auto;display:block}

a {text-decoration: none; display: inline-block;}

ul, li, ol {margin: 0; padding: 0; list-style: none;}

body {
    font-size: 1.5rem;
    line-height: 1.6;
    font-family: 'Roboto', sans-serif;
    background: url('../assets/bgStarWars.jpg');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}

// main menu list(people, movies, planets)
.topLevelLists {
    text-transform: uppercase;

    li {
        margin: 20px 0;
    }
}

// sublist styling(people, fimls, planets)
.subList {
    display: grid !important;
    grid-template-columns: repeat(auto-fit, minmax(175px, 1fr));
    grid-gap: 30px;
    justify-items: center;
    width: 100%;
}

// go back links and other
a {
    color: #FFE81F;
    padding: 5px 10px;
    transition: all .3s;
    background: #29292d;
    width: 100%;
    border: 1px solid transparent;

    &:hover,
    &:focus {
        color: #29292d;
        background: #FFE81F;
    }
}

// Style for a LINK to go one lever up
.goToUpperLevel {
    margin-bottom: auto;
}

// nextPrev pages button positioning
.nextPrevPages {
    margin: 0 0 50px 0;
}